@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.contact {
  &__grid {
    display: grid;
    gap: 2rem;
    @include m.tablet {
      grid-template-columns: 2fr 1fr;
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    align-items: center;

    @include m.tablet {
      align-items: start;
    }
  }

  &__heading {
    @include m.subtitle(0);
  }

  &__info {
    color: v.$white;
    display: flex;
    gap: 1rem;
  }

  &__icon {
    color: v.$orange;
  }
}
