@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.author {
  background-color: #252525;
  &__grid {
    display: grid;
    gap: 2rem;
    @include m.tablet {
      @include m.grid(2, 2rem);
      align-items: center;
    }
  }

  &__img {
    @include m.tablet {
      max-width: 25rem;
      margin: 0 auto;
    }
  }

  &__container {
    text-align: center;

    @include m.tablet {
      text-align: left;
    }
  }

  &__heading {
    @include m.subtitle(0);
    text-transform: capitalize;
  }

  &__text {
    position: relative;
    margin-top: 2rem;
    text-align: center;
    color: v.$white;

    @include m.tablet {
      margin-top: 0;
      text-align: left;
      padding: 5rem 0 0;
      &::before {
        position: absolute;
        top: 1rem;
        left: 0;
        content: "";
        display: block;
        width: 12rem;
        height: 0.2rem;
        background-color: v.$orange;
      }
    }
  }
  &__words {
    margin-bottom: 2rem;
  }

  &__star {
    display: flex;
    justify-content: center;
    gap: 1rem;
    font-weight: 900;
    font-size: 1.5rem;

    @include m.tablet {
      justify-content: flex-start;
    }
  }
  &__icon {
    color: v.$orange;
  }
}
