@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.header {
  background-image: linear-gradient(
      to right,
      rgb(0 0 0 / 0.8),
      rgb(0 0 0 / 0.4)
    ),
    url(../img/bg_header.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 2rem 0;

  @include m.desktop {
    height: 100vh;
    padding: 0;
  }

  &__nav {
    padding-top: 2rem;
    display: flex;
    gap: 2rem;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    @include m.tablet {
      flex-direction: row;
    }
  }

  &__link {
    text-decoration: none;
  }

  &__logo {
    display: flex;
    align-items: center;
    gap: 1rem;
  }

  &__img {
    max-width: 8rem;

    @include m.tablet {
      max-width: 4rem;
    }
  }
  &__name {
    color: v.$white;
  }

  &__text {
    margin-top: 5rem;
    @include m.tablet {
      @include m.grid(2, 1rem);
    }
  }

  &__title {
    text-align: center;
    color: v.$white;
    max-width: 30rem;
    font-size: 3rem;

    @include m.tablet {
      text-align: left;
    }
  }

  &__special {
    color: v.$orange;
  }

  &__button {
    @include m.button;
    display: block;
    @include m.tablet {
      display: inline-block;
    }
  }

  &__video {
    width: 20rem;
    margin: 2rem auto 0 auto;

    @include m.tablet {
      margin: 3rem 0 0 0;
    }
  }
}
