@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;

  @include m.tablet {
    flex-direction: row;
    gap: 5rem;
  }

  &--v {
    @include m.tablet {
      display: flex;
      gap: 1rem;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  &__link {
    color: v.$white;
    text-decoration: none;
    transition: color 250ms ease;

    &:hover {
      color: v.$orange;
    }
  }
}
