@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.about {
  background-color: #252525;
  &__grid {
    display: grid;
    gap: 2rem;
    @include m.tablet {
      grid-template-columns: 2fr 1fr;
      align-items: center;
    }
  }
  &__heading {
    padding-top: 5rem;
    @include m.subtitle(0);
    text-align: center;

    @include m.tablet {
      text-align: left;
    }
  }

  &__text {
    position: relative;
    margin-top: 2rem;
    text-align: center;

    max-width: 25rem;
    margin: 0 auto;

    @include m.tablet {
      margin: 0;
      text-align: left;
      padding: 5rem 0 0;
      &::before {
        position: absolute;
        top: 1rem;
        left: 0;
        content: "";
        display: block;
        width: 12rem;
        height: 0.2rem;
        background-color: v.$orange;
      }
    }
  }

  &__words {
    margin-bottom: 2rem;
    color: v.$white;
  }

  &__link {
    @include m.button;
  }
  &__img {
    @include m.tablet {
      height: 42rem;
      object-fit: cover;
    }
  }
}
