@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.interior {
  &--office {
    padding-bottom: 5rem;
    @include m.tablet {
      padding-bottom: 10rem;
    }
  }
  &__grid {
    display: grid;
    gap: 2rem;
    @include m.tablet {
      @include m.grid(3, 2rem);
      align-items: start;
    }
  }

  &__img {
    @include m.tablet {
      @include m.updateImg;
    }
  }

  &__container {
    color: v.$white;
    @include m.tablet {
      margin-top: 5rem;
    }
  }

  &__heading {
    text-align: center;
    @include m.subtitle(-32%);
    @include m.tablet {
      text-align: left;
    }
  }
  &__text {
    position: relative;
    margin-top: 2rem;
    text-align: center;

    @include m.tablet {
      margin-top: 0;
      text-align: left;
      padding: 3rem 0 3rem 5rem;
      &::before {
        position: absolute;
        top: 1rem;
        left: 1rem;
        content: "";
        display: block;
        width: 12rem;
        height: 0.2rem;
        background-color: v.$orange;
      }

      &::after {
        position: absolute;
        bottom: 0;
        right: 0;
        content: "";
        display: block;
        width: 12rem;
        height: 0.2rem;
        background-color: v.$orange;
      }
    }
  }

  &__words {
    margin-bottom: 2rem;
  }

  &__button {
    @include m.button;
  }
}
