.header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.4)), url(../img/bg_header.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 2rem 0;
}
@media screen and (min-width: 1024px) {
  .header {
    height: 100vh;
    padding: 0;
  }
}
.header__nav {
  padding-top: 2rem;
  display: flex;
  gap: 2rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .header__nav {
    flex-direction: row;
  }
}
.header__link {
  text-decoration: none;
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header__img {
  max-width: 8rem;
}
@media screen and (min-width: 768px) {
  .header__img {
    max-width: 4rem;
  }
}
.header__name {
  color: #e9e9e9;
}
.header__text {
  margin-top: 5rem;
}
@media screen and (min-width: 768px) {
  .header__text {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.header__title {
  text-align: center;
  color: #e9e9e9;
  max-width: 30rem;
  font-size: 3rem;
}
@media screen and (min-width: 768px) {
  .header__title {
    text-align: left;
  }
}
.header__special {
  color: #ffb13b;
}
.header__button {
  display: inline-block;
  color: #272d2d;
  text-align: center;
  text-decoration: none;
  background-color: #ffb13b;
  border: 2px solid #ffb13b;
  padding: 1rem 2rem;
  border-radius: 2rem;
  transition: background-color 500ms ease;
  display: block;
}
.header__button:hover {
  background-color: #e9e9e9;
}
@media screen and (min-width: 768px) {
  .header__button {
    display: inline-block;
  }
}
.header__video {
  width: 20rem;
  margin: 2rem auto 0 auto;
}
@media screen and (min-width: 768px) {
  .header__video {
    margin: 3rem 0 0 0;
  }
}

.footer__grid {
  display: grid;
  gap: 2rem;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.footer__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media screen and (min-width: 768px) {
  .footer__logo {
    flex-direction: row;
  }
}
.footer__img {
  max-width: 3rem;
}
.footer__name {
  font-size: 3rem;
  color: #e9e9e9;
}
.footer__text {
  position: relative;
  margin-top: 2rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .footer__text {
    margin-top: 0;
    text-align: left;
    padding: 5rem 0 0;
  }
  .footer__text::before {
    position: absolute;
    top: 3rem;
    left: 0;
    content: "";
    display: block;
    width: 12rem;
    height: 0.2rem;
    background-color: #ffb13b;
  }
}
.footer__words {
  margin-bottom: 2rem;
  color: #e9e9e9;
}
.footer__heading {
  text-align: center;
  margin: 2rem 0;
  color: #e9e9e9;
}
@media screen and (min-width: 768px) {
  .footer__heading {
    text-align: left;
    margin: 0 0 8rem;
  }
}
.footer__heading--none {
  display: none;
}
@media screen and (min-width: 768px) {
  .footer__heading--none {
    margin-bottom: 13rem;
    display: block;
  }
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media screen and (min-width: 768px) {
  .navigation {
    align-items: flex-start;
  }
}
.navigation__link {
  text-decoration: none;
  color: #e9e9e9;
}
.navigation__link:hover {
  color: #ffb13b;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media screen and (min-width: 768px) {
  .nav {
    flex-direction: row;
    gap: 5rem;
  }
}
@media screen and (min-width: 768px) {
  .nav--v {
    display: flex;
    gap: 1rem;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
.nav__link {
  color: #e9e9e9;
  text-decoration: none;
  transition: color 250ms ease;
}
.nav__link:hover {
  color: #ffb13b;
}

.social {
  display: none;
}
@media screen and (min-width: 768px) {
  .social {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    gap: 2rem;
  }
}
@media screen and (min-width: 768px) {
  .social--h {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.social__media {
  background-color: #ffb13b;
  text-decoration: none;
  border-radius: 50%;
  transition: background-color 250ms ease;
}
.social__media:hover {
  background-color: #e9e9e9;
}
.social__icon {
  width: 3rem;
  height: 3rem;
  color: #272d2d;
  font-size: 1.2rem;
  padding: 1rem;
}

.interior--office {
  padding-bottom: 5rem;
}
@media screen and (min-width: 768px) {
  .interior--office {
    padding-bottom: 10rem;
  }
}
.interior__grid {
  display: grid;
  gap: 2rem;
}
@media screen and (min-width: 768px) {
  .interior__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    align-items: start;
  }
}
@media screen and (min-width: 768px) {
  .interior__img {
    height: 50rem;
    width: auto;
    object-fit: cover;
