@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.footer {
  &__grid {
    display: grid;
    gap: 2rem;
    align-items: center;
    @include m.tablet {
      @include m.grid(4, 2rem);
    }
  }

  &__logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    @include m.tablet {
      flex-direction: row;
    }
  }

  &__img {
    max-width: 3rem;
  }

  &__name {
    font-size: 3rem;
    color: v.$white;
  }

  &__text {
    position: relative;
    margin-top: 2rem;
    text-align: center;

    @include m.tablet {
      margin-top: 0;
      text-align: left;
      padding: 5rem 0 0;
      &::before {
        position: absolute;
        top: 3rem;
        left: 0;
        content: "";
        display: block;
        width: 12rem;
        height: 0.2rem;
        background-color: v.$orange;
      }
    }
  }

  &__words {
    margin-bottom: 2rem;
    color: v.$white;
  }

  &__heading {
    text-align: center;
    margin: 2rem 0;
    color: v.$white;
    @include m.tablet {
      text-align: left;
      margin: 0 0 8rem;
    }

    &--none {
      display: none;

      @include m.tablet {
        margin-bottom: 13rem;
        display: block;
      }
    }
  }
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;

  @include m.tablet {
    align-items: flex-start;
  }

  &__link {
    text-decoration: none;
    color: v.$white;

    &:hover {
      color: v.$orange;
    }
  }
}
