@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.furniture {
  padding-bottom: 5rem;

  @include m.tablet {
    padding-bottom: 10rem;
  }

  &__grid {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    @include m.tablet {
      @include m.grid(3, 2rem);
    }
  }

  &__container {
    order: 3;
    color: v.$white;
    @include m.tablet {
      order: unset;
    }
  }

  &__heading {
    text-align: center;
    @include m.subtitle(30%);

    @include m.tablet {
      text-align: right;
    }
  }

  &__text {
    position: relative;
    margin-top: 2rem;
    text-align: center;

    @include m.tablet {
      margin-top: 0;
      text-align: left;
      padding: 3rem 0 3rem 5rem;
      &::before {
        position: absolute;
        top: 1rem;
        right: 1rem;
        content: "";
        display: block;
        width: 12rem;
        height: 0.2rem;
        background-color: v.$orange;
      }

      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        content: "";
        display: block;
        width: 12rem;
        height: 0.2rem;
        background-color: v.$orange;
      }
    }
  }

  &__words {
    margin-bottom: 2rem;
  }

  &__button {
    @include m.button;
  }

  &__img {
    @include m.tablet {
      @include m.updateImg;
    }
  }
}
